



  :root {
    
      --color-base: #ffffff;
    
      --color-base-offset: #f2f2f2;
    
      --color-base-text: #202020;
    
      --color-base-text-offset: #413f3f;
    
      --color-primary: #273648;
    
      --color-primary-offset: #371fda;
    
      --color-primary-text: #ffffff;
    
      --color-primary-text-offset: #e8ffff;
    
    
      --font-base: Lato;
    
      --font-heading: Source Sans Pro;
    
  }


html[data-mode='dark'] {
  
    --color-base: #121212;
  
    --color-base-offset: #1e1e1e;
  
    --color-base-text: #ffffff;
  
    --color-base-text-offset: #bfbfbf;
  
    --color-primary: #b91134;
  
    --color-primary-offset: #521ec5;
  
    --color-primary-text: #ffffff;
  
    --color-primary-text-offset: #e8ffff;
  
}